.logoContainer {
    height: 75%;
    margin-left: 7px;
}

#logo{
    height: 90%;
}

#hamburger{
    display: none;
}

.navButtonsContainer {
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: center;
    flex-direction: row;
}

.navButtons{
    margin-top: auto;
    margin-right: 70px;
    font-size: 1.25em;
    border: none;
    background-color: transparent;
    cursor: pointer;
}

#userButtonContainer{
    overflow: hidden;
    position: absolute;
    right: 0px;
    top: 86px;
    height: 54px;
    width: 200px;
    transition: 0.5s ease-out;

}

#userButtonContainer:hover{
    height: 320px;
}

#signButton{
    display: none;
    background-color: #FFCD51;
    height: 44px;
    padding: 0px 9px 0px 9px;
    font-size: 1.25em;
    border: 2px black solid;
    border-radius: 30px;
    cursor: pointer;
}

#userInfo{
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    align-content: flex-start;
}

#userInfo * {
    margin: 5px;
    cursor: pointer;
}


#userButtonContainer:hover > #userInfo :nth-child(1){
    transform: rotate(180deg);
}

#userDropDown{
    background-color: #FFCD51;
    padding-left: 30px;
    padding-bottom: 30px;
}

.userDropDownBtn{
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 3px solid black;
    height: 60px;
    width: 80%;
}

#userDropDownArrow{
    transition: 0.5s ease-out;
}


@media only screen and (max-width: 800px) {
    /* For tablets and phones: */
    
    .logoContainer {
        position: absolute;
        top: 0px;
        left: 0px;
        background: linear-gradient(0deg, rgba(0,0,0,0) 0%, rgba(255,206,80,0.6) 50%, rgba(0,0,0,0) 100%);
        margin: 0px;
        height: 100%;
        width: 100%;
    }

    #logo{
        position: absolute;
        left: 35%;
        top: 9%;
        height: 80%;
    }

    #userInfo>*{
        display: none;
    }

    #userButtonContainer{
        position: absolute;
        top: 0;
        left: 0;
    }

    #userButtonContainer:hover{
        height: 400px;
    }

    #hamburger{
        display: block;
        position: relative;
        top: 15px;
        left: 15px;
        margin-bottom: 85px;
    }

    #userDropDown{
        position: relative;
    }

    .navButtonsContainer{
        display: none;
    }

}