.productDescriptionComponent{
    display: flex;
    padding: 20px;
}

.productImgsContainer{
    min-height: 300px;
    width: 270px;
    margin-right: 30px;
}

#mainImage{
    max-width: 100%;
}

.smallImageContainer{
    height: 100px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 5px;
}

.scrollerArrows{
    position: relative;
    top: 30%;
    cursor: pointer;
    transition: 0.1s ease-in-out;
}

.scrollerArrows:active{
    transform: scale(1.2);
}

#arrowLeftScroll{
    margin-right: 10px;
}

#arrowRightScroll{
    margin-left: 10px;
}

#smallImgScrollerContainer{
    display: flex;
    height: 90px;
    overflow: scroll;
    overflow-y: hidden;
    overflow-x: hidden;
    scrollbar-width: none;
    scroll-behavior: smooth;
    align-items: center;
}

.scrollImg{
    width: 85px;
    padding: 0px 5px 0px 5px;
}


.productBtnsContainer{
    display: block;
    text-align: center;
    margin-left: 20px;
}

.productBtnsContainer > button{
    margin-bottom: 5px;
    min-width: 200px;
}

.productReviewComponent{
    padding: 20px;
}

#addReviewContainer{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.productRecommendation{
    padding-top: 30px;
    display: flex;
    justify-content: space-between;
    flex: 100% 0% 0%;
    flex-wrap: wrap;
}

.productRecommendationDivs{
    text-align: center;
    min-width: 235px;
    margin: 5px 0px 20px 0px;
}

.productRecommendationDivs img{
    height: 40px;
}

.addImage{
    width: 120px;
    height: 100px;
    background-color: lightgray;
    border: 1px solid black;
    border-radius: 10px;
    cursor: pointer;
}

@media only screen and (max-width: 800px) {

    .productDescriptionComponent{
        flex-wrap: wrap;
        justify-content: center;
    }

    .productImgsContainer{
        margin: 0px;
    }

    .productDescriptionMainContainer{
        flex-wrap: wrap;
        justify-content: center;
    }

    .productBtnsContainer{
        margin: 15px 0px 0px 0px;
    }

    .productRecommendation{
        justify-content: center;
    }

}